const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			formNode: {
				title: '',
				content: '',
				board_id: '',
				topic_id: ''
			}
		}
	},
	onLoad(e) {
		console.log(e);
		this.formNode.board_id = e.id;
		this.name = e.name;
	},
	methods: {
		// 上传图片
		getPhoto() {
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'],
				success: res => {
					uni.showToast({
						title: '图片上传中',
						icon: 'loading'
					});
					Promise.all(
						res.tempFiles.map(item => {
							return new Promise((resolve, reject) => {
								uni.uploadFile({
									url: `${helper.requestUrl}/posts/uploads`,
									filePath: item.path,
									name: 'file',
									header: {
										authorization: app.globalData.token
									},
									success: res => {
										console.log(res)
										resolve({
											path: JSON.parse(res.data).data
										});
									}
								});
							});
						})
					).then(e => {
						uni.hideToast();
						this.imgInfo = e[0].path;
						console.log(this.imgInfo, '图片')
					}).catch(err => console.log(err));
				}
			});
		},
		onEditorReady() {
			console.log(this.imgInfo, '****')
			uni.createSelectorQuery().select('#editor').context((res) => {
				console.log(res)
				this.editorContext.insertImage({
					src: this.imgInfo.path,
				})
				// this.editorCtx.setContents({
				// 	html:that.info.gongsijieshao,
				// 	success:(res)=> {
				// 		console.log(res)
				// 	},
				// 	fail:(res)=> {
				// 		console.log(res)
				// 		},
				// })
				// this.editorCtx = res.context
				// this.editorCtx.insertImage({
				//   src: this.imgInfo.path
				// })
				// console.log(this.editorCtx)
			}).exec()
		},
		contentChange(e) {
			console.log(e.detail);
		},
		inputValue(e) {
			let formNode = this.formNode
			let name = e.currentTarget.dataset.name
			let value = e.detail.value
			formNode[name] = value
			this.formNode = formNode
		},
		// 发帖
		post() {
			uni.request({
				url: `${helper.requestUrl}/posts/send`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: this.formNode,
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res);
					if (res.data.status_code == '200') {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						}, 2000)
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		}
	}
}
